@charset 'utf-8';

.form-group {
  label {
    font-weight: 600;
  }
}

.form-control {
  padding: $input-btn-padding-y ($input-btn-padding-x / 2);
}

[data-toggle=buttons] {
  .btn {
    input[type="radio"],
    input[type="checkbox"] {
      position: absolute;
      clip: rect(0,0,0,0);
      pointer-events: none;
    }
  }
}
