@charset 'utf-8';

// Card Pill
////////////

// Mixin to set the pill gradient
@mixin card-pill-bg-gradient($class) {
  #{$class} {
    background-color: transparent;
    background-image: linear-gradient(to right, rgba($card-cap-bg, 0) 5%, rgba($card-cap-bg, 0.08) 50%);
  }

  a#{$class} {
    &:active,
    &.active {
      transform: scale(0.98);
      background-image: linear-gradient(to right, rgba(theme-color("secondary"), 0.02) 5%, rgba(theme-color("secondary"), 0.3) 70%);
    }
  }
}

// Card border radius has to be smaller to correspond to the card-pill
.card {
  @include border-radius($card-border-radius - $card-border-width);
}

a.card {
  @include hover {
    text-decoration: none;
  }
}

// The card pill displays an icon at the left
.card-pill {
  position: relative;
  display: flex;
  align-items: center;
  margin: -$card-border-width;
  color: color("light");
  font-weight: $font-weight-bold;
  border-bottom: none;
  background-color: $card-cap-bg;
  border: $card-border-width solid $card-cap-bg;

  @include media-breakpoint-down(sm) {
    flex-wrap: wrap;
  }

  @include border-radius($card-border-radius);

  &.card-pill-no-icon {
    padding-left: $card-spacer-x;
  }

  .flag {
    margin: ($spacer * -3) 0 ($spacer * -3) ($card-spacer-x * 2);
    color: $card-cap-bg;

    @include flag-size(60px);
  }
}

.card-pill-white {
  background-color: color("light");
  color: $card-cap-bg;
}

a[href].card-pill {
  @include hover-focus {
    text-decoration: none;
    color: color("light");
  }
}

// Variation of the pill with Primary colors
.card-pill-simple {
  color: theme-color("primary");
  font-weight: $font-weight-normal;
  border: 0;

  .card-pill-icon .icon {
    color: theme-color("primary");
  }
}

@include card-pill-bg-gradient(".card-pill-simple");

a[href].card-pill-simple {
  @include hover-focus {
    text-decoration: none;
    color: theme-color("primary");
  }
}

.card-pill-title {
  span {
    white-space: nowrap;

    // space the spans correctly inside card-pill title
    &:not(:last-child) {
      margin-right: $card-spacer-x * 2;

      @include media-breakpoint-down(sm) {
        margin-right: $card-spacer-x;
      }
    }
  }

  // Make sure when we're centering the text, the icon is not pushing things in
  // one direction
  &.mx-auto {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0 calc(#{$card-pill-icon-size} + #{$card-spacer-x});
    text-align: center;
  }
}

// The icon, pushed against the left border
.card-pill-icon {
  line-height: 0;
  margin-right: $card-spacer-x;

  .icon {
    justify-content: center;
    align-items: center;
    width: $card-pill-icon-size;
    height: $card-pill-icon-size;
    border-radius: 50%;
    background-color: color("light");
    color: $card-cap-bg;

    svg {
      width: $card-pill-icon-size * 0.6875;
      height: $card-pill-icon-size * 0.6875;
    }
  }
}

// Card
///////

.card-title {
  margin-top: $card-spacer-y * 2;
  margin-bottom: $card-spacer-y * 3;
  text-align: center;
}

.card-icon {
  text-align: center;

  @include icon-size(60px);
}

.card-actions {
  margin-top: $card-spacer-y;
}

// Card List Item
/////////////////

// This wraps the list items
.card-list {
  @include list-unstyled();
}

// When the card is only a pill in a list, closed state, hides the card-body
// add .open class to animate opening
.card-list-item {
  margin-bottom: $spacer;

  // Apply some styles when card is ONLY NOT OPEN
  &:not(.card-open) {
    background: none;
    border-color: transparent;

    .card-pill-icon {
      .icon {
        background-color: $card-cap-bg;
        color: color("light");
      }
    }

    .card-pill {
      padding: 0;
      border-color: $white;
    }

    .card-pill-title {
      color: $body-color;
    }

    @include card-pill-bg-gradient(".card-pill");
  }

  .card-pill {
    padding-top: 0;
    padding-bottom: 0;
  }

  // Hide the button at the right of the pill ONLY WHEN OPEN
  &.card-open {
    .card-pill {
      border: 2px solid $card-cap-bg;

      .btn {
        display: none;
      }
    }
  }

  // Display a flag when the status of the list item is set
  .flag {
    color: color("light");

    svg {
      fill: $blue;
    }

    @include flag-size(50px);
  }
}

.card-list-simple-item {
  &:not(.card-open) {
    background: none;
    border: 0;
  }
}

// Invert colors with theme colors
@each $color, $value in $theme-colors {
  .card-#{$color} {
    background: color(#{$color}-light);
    border-color: $value;

    // Automaticly calculate color value based on contrast with background
    @include color-contrast($value);

    // Base is secondary color, just apply to other colors
    @if ($color != 'secondary') {
      .card-pill {
        background-color: $value;
        border-color: $value;
      }

      .card-pill-simple {
        background: none;
        background-image: linear-gradient(to right, rgba($value, 0) 5%, rgba($value, 0.08) 50%);
      }

      .card-pill-icon {
        .icon {
          color: $value;
        }
      }

      .card-pill-simple {
        color: $value;
      }

      &.card-list-item {
        &:not(.card-open) {
          .card-pill {
            background-image: linear-gradient(to right, rgba($value, 0) 5%, rgba($value, 0.08) 50%);
          }

          a.card-pill {
            &:active,
            &.active {
              background-image: linear-gradient(to right, rgba($value, 0.02) 5%, rgba($value, 0.3) 70%);
            }
          }

          .card-pill-icon {
            .icon {
              background-color: $value;
            }
          }
        }

        &.card-open {
          .card-pill {
            border-color: $value;
          }
        }
      }

      &.card-list-simple-item {
        a.card-pill {
          @include hover-focus-active {
            color: $value;
          }

          &:active,
          &.active {
            background-image: linear-gradient(to right, rgba($value, 0.02) 5%, rgba($value, 0.3) 70%);
          }
        }
      }
    }
  }
}
